<template>
  <div class="onlineBooking">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      fixed
      @click-left="onClickLeft"/>
    <div class="top address">
      <div v-if="this.time != ''"><span>{{this.time.codeText}}</span></div>
      <div v-if="this.address != ''"><span>{{this.address.codeText}}</span></div>
    </div>
    <div class="top">
      <van-list
        v-model="loading"
        :finished="finished"
        :offset= "10"
        @load="onLoad"
      >
      <div class="personal-info between"
      v-for="(item, index) in list" :key="index">
        <div class="avatar">
          <img :src="$store.state.system.sftpPath + item.zp"
          :onerror="defaultImg" alt="">
        </div>
        <div class="info">
          <div class="con">
            <div class="name">{{item.userName}}</div>
            <div class="result">{{item.dutyDate}}</div>
            <div class="result">{{item.codeText}}</div>
            <div class="result">
              {{item.isAppointment === '' ? '' : item.isAppointment === '' ? '' : ''}}
            </div>
          </div>
          <div class="btn-group" v-if="item.isAppointment != '' && item.isAppointment != null">
            <van-button size="small" class="btn"
            @click="addOnline(item)">
              {{item.isAppointment}}
            </van-button>
          </div>
        </div>
      </div>
      </van-list>
    </div>
    <div v-if="this.list.length == 0">
      <no-data></no-data>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { getStuOnLine, addRecord, stuInfo, dutyTime, jPush } from '@/services/health';
import noData from '@/components/noData/noData.vue';

export default {
  name: 'onlineBooking',
  data() {
    return {
      defaultImg: `this.src="${require('@/assets/tx@2x.png')}"`,
      loading: false,
      finished: false,
      pageNum: 0,
      list: [],
      stu: {},
      state: '',
      duty: {
        type: 'dutyTime',
      },
      time: {},
      address: {},
      add: {
        planId: '', // id
        schoolId: '', // 
        deptId: '', // 
        deptName: '', // 
        professionId: '', // 
        professionName: '', // 
        classId: '', // 
        className: '', // 
        gradeId: '', // 
        gradeName: '', // 
        stuId: '', // 
        stuName: '', // 
        stuSexCode: '', // code12
        stuSexText: '', // Text
        appointDurationCode: '', // 
        appointDurationText: '', // 
        isAppoint: '', // (012)
      },
      jPushData: {
        userId: '',
        mk: 'xljk',
        message: '',
      },
    };
  },
  components: {
    'no-data': noData,
  },
  computed: {
    ...mapGetters('system', ['userInfo']),
    // param() {
    //   return {
    //     stuId: this.userInfo.id,
    //   };
    // },
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    async getDutyTime() {
      const { data } = await dutyTime(this.duty);
      [this.time] = data.dutyArea;
      [this.address] = data.dutyDate;
    },
    // async getOnLine(msg) {
    //   const data = await getStuOnLine(msg);
    //   this.list = data.data;
    // },
    async onLoad() {
      const data = await getStuOnLine({
        stuId: this.userInfo.id,
        pageNum: this.pageNum += 1,
      });
      this.list = this.list.concat(data.data);
      // 
      this.loading = false;
      // 
      if (this.list.length >= data.total) {
        this.finished = true;
      }
    },
    async getStuInfo() {
      const { data } = await stuInfo({ stuId: this.userInfo.id });
      [this.stu] = data;
    },
    async onLine(item, msg) {
      const data = await addRecord(msg);
      if (data.htppCode === '200' || 200) {
        this.$toast.success('');
      } else {
        this.$toast(data.msg);
      }
      this.jPushData.userId = item.userId;
      await jPush(this.jPushData);
      this.$router.go(-1);
    },
    addOnline(item) {
      if (item.isAppointment === '') {
        this.state = '1';
        this.jPushData.message = `${this.stu.userName}${this.$moment(item.dutyDate).format('YYYYMMDD')}`;
      } else if (item.isAppointment === '') {
        this.state = '2';
        this.jPushData.message = `${this.stu.userName}${this.$moment(item.dutyDate).format('YYYYMMDD')}`;
      }
      this.add = {
        planId: item.id, // id
        schoolId: this.stu.schoolId, // 
        deptId: this.stu.orgId, // 
        deptName: this.stu.DWMC, // 
        professionId: this.stu.proId, // 
        professionName: this.stu.ZYMC, // 
        classId: this.stu.classId, // 
        className: this.stu.BJMC, // 
        gradeId: this.stu.gradeId, // 
        gradeName: this.stu.gradeName, // 
        stuId: this.stu.id, // 
        stuName: this.stu.userName, // 
        stuSexCode: this.stu.XBM, // code12
        stuSexText: this.stu.XBMTEXT, // Text
        appointDurationCode: item.code, // 
        appointDurationText: item.codeText, // 
        isAppoint: this.state, // (012)
      };
      this.onLine(item, this.add);
    },
  },
  mounted() {
    // this.getOnLine(this.param);
    this.getStuInfo();
    this.getDutyTime();
  },
};
</script>

<style lang="scss" scoped>
  .onlineBooking{
    padding:46px 0 60px;
  }
  .success {
    color: #51bf61 !important;
  }
  .warning{
    color: #ea9542 !important;
  }
  .danger{
    color: #ff5757 !important;
  }
  .between{
    margin:0 12px;
  }
  .top{
    margin-top: 11px;
    background: #fff;
  }
  .address{
    padding:10px 12px;
    >div{
      line-height: 30px;
      color:#333;
      font-size: 14px;
      >span{
        color:#777;
      }
    }
  }
  .personal-info{
    padding:20px 0px 0px;
    overflow: hidden;
    position: relative;
    .avatar{
      width: 48px;
      height: 48px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 14px;
      float: left;
    }
    .info{
      width:calc(100% - 62px);
      float: left;
      border-bottom:1px solid #e7e7e7;
      .name{
        font-size: 16px;
        color: #333;
        margin-bottom: 6px;
      }
      .result{
        font-size: 15px;
        color:#999;
        line-height: 25px;
      }
    }
    &:last-child{
      .info{
        border: 0;
      }
    }
    .con{
      float: left;
      padding-bottom: 20px;
    }
    .date{
      font-size: 14px;
      color:#999;
      float: right;
    }
    .btn-group{
        position: absolute;
        bottom: 20px;
        right: 0;
        .btn{
            font-size: 16px;
            border-radius: 2px;
            background-color: #1890ff;
            color: #FFFFFF;
            padding: 0px 12px;
        }
    }
  }
</style>
