import request from '@/utils/request';

/**
 * 
 * @param  {[string]} stuId [id]
 * @return {[type]}         [description]
 */
export function getStuOnLine(data) {
  return request({
    method: 'put',
    url: '/api/mobileMindhealthyPlan/read/getStuOnLine',
    data,
  });
}

